import React from 'react';
import './AppHome.css';
import TopBar from '../TopBar/TopBar';
import NavBar from '../NavBar/NavBar';
import HomeTab from '../HomeTab/HomeTab';
import ManageTab from '../ManageTab/ManageTab';
import ReportsTab from '../ReportsTab/ReportsTab';
import ProfileTab from '../ProfileTab/ProfileTab';

import EditTaskForm from '../EditTaskForm/EditTaskForm';

class AppHome extends React.Component {
    constructor() {
        super();
        this.state = {
            route: 'home',
            formContentClass: 'displayNone'
        };
    }

    changeRoute = (route) => {
        this.setState({
            route: route,
            formContentCLass: 'displayNone'
        });
    }

    displayForm = (displayStatus) => {
        if (displayStatus === 'hide') {
            this.setState({formContentClass: 'displayNone'});
        } else {
            this.setState({formContentClass: ''});
        }
    }

    render() {
        
        switch (this.state.route) {
            case 'home':
                return (
                    <div className='appHome'>
                          <TopBar />
                          <div className='appBodyContainer'>
                              <NavBar newRoute={this.changeRoute} />
                              <HomeTab displayForm={this.displayForm} />
                              <div className='rightColumn'>
                                  <div className={this.state.formContentClass}>
                                    <EditTaskForm displayStatus={this.displayForm} />
                                  </div>
                              </div>
                        </div>
                    </div>
                  );

            case 'manage':
                return (
                    <div className='appHome'>
                          <TopBar />
                          <div className='appBodyContainer'>
                              <NavBar newRoute={this.changeRoute} />
                              <ManageTab />
                              <div className='rightColumn'>
                                  Manage
                              </div>
                        </div>
                    </div>
                  );

            case 'reports':
                return (
                    <div className='appHome'>
                          <TopBar />
                          <div className='appBodyContainer'>
                              <NavBar newRoute={this.changeRoute} />
                              <ReportsTab />
                              <div className='rightColumn'>
                                  Reports
                              </div>
                        </div>
                    </div>
                  );

            case 'profile':
                return (
                    <div className='appHome'>
                          <TopBar />
                          <div className='appBodyContainer'>
                              <NavBar newRoute={this.changeRoute} />
                              <ProfileTab loginStatus={this.props.loginStatus}/>
                              <div className='rightColumn'>
                                  Profile
                              </div>
                        </div>
                    </div>
                  );
                  
            default: return null;
        }
    }
}

export default AppHome;