import React from 'react';
import { ChevronLeft } from 'react-bootstrap-icons'; 

class EditTaskForm extends React.Component {
    constructor() {
        super();
        this.state = {};
    }

    handleSubmit = (e) => {
        this.props.changeRoute('');
        e.preventDefault();
    }

    hideForm = (e) => {
        e.preventDefault();
        this.props.displayStatus('hide')
    }

    render() {
        return (
            <form className='flexCenter popupForm' onSubmit={this.handleSubmit} >

                <div className='formContainer'>
            
                    <div className='popupFormNav'>
                        <button className='iconBtn' onClick={this.hideForm}>
                            <ChevronLeft className='icon' size={25} /> Back
                        </button>
                    </div>

                    <div className='flexCenter'>
            
                        <div>
                            <label>Label 1</label><br />
                            <input type='text' value='input#1' />
                        </div>
                    
                        <div>
                            <label>Label 1</label><br />
                            <input type='text' value='input#2' />
                        </div>

                        <p className='formValidationText'>Invalid</p>

                        <button className='formSubmitBtn' type='submit'>Save Changes</button>

                    </div>
                </div>
            </form>
        )
    }
}

export default EditTaskForm;